@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: bold;
}

html {
  font-weight: 300;
}

body {
  margin: 0;
}

.form:not(:last-child) {
  margin-top: 20px;
  margin-bottom: 20px;
}

.searchbar__container {
  position: relative;
}

.searchbar {
  box-sizing: border-box;
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  outline: none;
  width: 100%;
  font-family: Avenir, Arial, sans-serif;
  font-weight: 400;
  transition: all .15s ease-in-out;
  box-shadow: 0 1px 8px #3d4e611a;
}

.searchbar--big {
  background-position: 26px;
  background-size: 19px;
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}

.searchbar--small {
  background-position: 13px;
  background-size: 11px;
  height: 42px;
  padding-left: 34px;
  font-size: 14px;
  line-height: 19px;
}

.searchbar--big:hover, .searchbar--small:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.searchbar--small:focus, .searchbar--big:focus {
  color: #3d4e61;
  border-color: #e1e7ed;
  border-radius: 3px;
  font-weight: 900;
  box-shadow: 0 4px 4px #00000040;
}
